<template>
  <div>
    <div class="md" v-html="displayHTML"></div>
  </div>
</template>

<script>
import Prism from 'prismjs'
import marked from 'marked'

export default {
  props: ['content'],

  computed: {
    displayHTML () {
      const { content } = this

      const parsed = marked(content, {
        highlight: function (code, lang) {
          if (!Prism.languages[lang]) {
            return code
          }

          const formatted = Prism.highlight(code, Prism.languages[lang], lang)

          return formatted
        }
      })

      const correctedClasses = parsed.replace(/<pre><code\sclass="language-(.*?)">/gi, '<pre class="language-$1"><code class="language-$1">')

      return correctedClasses
    }
  }
}
</script>

<style lang="scss" scoped>
.md {
  /deep/ .embed {
    iframe {
      position: absolute; 
      top: 0; 
      left: 0; 
      width: 100%; 
      height: 100%;
    }
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    max-width: 100%;
  }

  /deep/ img {
   width: 100%;
  }
}
</style>
